import React from 'react';
import { Card, Row, Col, Table } from 'antd';
import styled from 'styled-components';
import { useStoreState } from '../store';
import { filterProposeEvents, filterVoteEvents } from '../event';
import _ from 'lodash';
import { ProposeEvent, VoteEvent } from '../types';
import { ColumnProps } from 'antd/es/table';
import { Hex } from '../component/Hex';

function shortHex(hex: string) {
  return <Hex data={hex} />;
}

const DashboardWrapper = styled.div`
  padding: 16px 0;
`;

function ProposeTable(props: { events: ProposeEvent[] }) {
  const columns: ColumnProps<ProposeEvent>[] = [
    { title: 'Name', key: 'eventName', dataIndex: 'eventName' },
    { title: 'Round', key: 'tag.roundId', dataIndex: 'tag.roundId' },
    {
      title: 'Proposer',
      key: 'tag.proposer',
      dataIndex: 'tag.proposer',
      render: shortHex,
    },
    {
      title: 'Hash',
      key: 'tag.hash',
      dataIndex: 'tag.hash',
      render: shortHex,
    },
  ];
  return (
    <Table
      size="small"
      pagination={false}
      scroll={{ y: 180 }}
      columns={columns}
      dataSource={props.events}
    />
  );
}

function VoteTable(props: { events: VoteEvent[] }) {
  const columns: ColumnProps<VoteEvent>[] = [
    { title: 'Name', key: 'eventName', dataIndex: 'eventName' },
    { title: 'Round', key: 'tag.roundId', dataIndex: 'tag.roundId' },
    {
      title: 'Voter',
      key: 'tag.voter',
      dataIndex: 'tag.voter',
      render: shortHex,
    },
    {
      title: 'Hash',
      key: 'tag.hash',
      dataIndex: 'tag.hash',
      render: shortHex,
    },
  ];
  return (
    <Table
      size="small"
      pagination={false}
      scroll={{ y: 180 }}
      columns={columns}
      dataSource={props.events}
    />
  );
}

export const Dashboard = () => {
  const { activeEpoch, activeRound, events } = useStoreState(
    state => state.tracer,
  );

  const filter = _.isNil(activeEpoch)
    ? {}
    : _.isNil(activeRound)
    ? { epochId: activeEpoch }
    : { epochId: activeEpoch, roundId: activeRound };

  return (
    <DashboardWrapper>
      <Row gutter={16}>
        <Col md={12} sm={24}>
          <Card title="Received proposal">
            <ProposeTable events={filterProposeEvents(events, filter)} />
          </Card>
        </Col>
        <Col md={12} sm={24}>
          <Card title="Received vote">
            <VoteTable events={filterVoteEvents(events, filter)} />
          </Card>
        </Col>
      </Row>
    </DashboardWrapper>
  );
};
